@import url('https://fonts.googleapis.com/css2?family=Big+Shoulders+Text:wght@100&display=swap');

*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  justify-content: center;
  scroll-behavior: smooth;
  overflow-x: hidden;

}
body{
  background: rgb(248, 239, 225);
}
body::-webkit-scrollbar {
  width: 5px;
  /* width of the entire scrollbar */
}

body::-webkit-scrollbar-track {
  background: black;
  /* color of the tracking area */
}

body::-webkit-scrollbar-thumb {
  background: rgb(193, 121, 46);
  background: -moz-linear-gradient(87deg, rgba(193, 121, 46, 1) 9%, rgba(237, 174, 109, 1) 23%, rgba(237, 174, 109, 1) 37%, rgba(237, 174, 109, 1) 58%, rgba(237, 174, 109, 1) 74%, rgba(193, 121, 46, 1) 89%);
  background: -webkit-linear-gradient(87deg, rgba(193, 121, 46, 1) 9%, rgba(237, 174, 109, 1) 23%, rgba(237, 174, 109, 1) 37%, rgba(237, 174, 109, 1) 58%, rgba(237, 174, 109, 1) 74%, rgba(193, 121, 46, 1) 89%);
  background: linear-gradient(87deg, rgba(193, 121, 46, 1) 9%, rgba(237, 174, 109, 1) 23%, rgba(237, 174, 109, 1) 37%, rgba(237, 174, 109, 1) 58%, rgba(237, 174, 109, 1) 74%, rgba(193, 121, 46, 1) 89%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#c1792e", endColorstr="#c1792e", GradientType=1);

  /* color of the scroll thumb */
  border-radius: 20px;
  /* roundness of the scroll thumb */
  /* creates padding around scroll thumb */
}

